body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.background{
  background-color: black;
  display: flex;
  flex-direction: column;
}

.text{
  padding-top: 150px;
  padding-bottom: 150px;
  color: white;
  text-align: center;
  font-family: 'Avenir';
  font-size: 50px;
  justify-content: center;
  align-items: center;
}

.quote{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.quoteText{
  color: black;
  font-family: 'Avenir';
  font-size: 20px;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 70px;
  padding-bottom: 70px;
}

.root{
  display: flex;
  flex-direction: column;
}

.footertext{
  font-family: 'Avenir';
  font-size: 12px;
}

.footercss{
  display: flex;
  align-items: center;
  justify-content: center;
}

.box{
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: black;
  width: 200px;
  height: 250px;
  border: 3px solid grey;
}

.boxText{
  color: white;
}

.box:hover{
  background-color: orange;
}

.flexClass{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-content: space-around;
  flex-wrap:wrap;
  margin-top: 50px;
  margin-bottom: 150px;
}

.centerClass{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.buttonClass{
  padding-left: 25px;
  padding-right: 25px;
}

